//* ------------------------------- [Variables] --------------------------------
$minWidth: 320px;
$maxWidth: 1920px;
$maxWidthContainer: 1110px;
$headerHeight: 4rem;
$footerHeight: 4rem;
$sidebarWidth: 4rem;
$titleHeight: 3rem;
$min-height: calc(100% - ($headerHeight + $footerHeight));
$bg-color: #fbf9f4;
//* ---------------------------- [Font Variables] ------------------------------
$font-path: '../fonts';
$font-defoult: 'Inter', sans-serif;
$font-size: 1.5rem;
$font-family-secondary: 'Arial', sans-serif;
$font-weight-defoult: 500;
//* ------------------------------ [Font color] --------------------------------
$font-color-defoult: rgb(0, 0, 0);
$font-color-secondary: rgb(0, 70, 70);
$font-color-other: #e1b69e;
//* -------------------------------- [Color] -----------------------------------
$color: rgb(0, 70, 70);
$color-second: #1c153f;
